.breadcrumbs {
  padding-top: 10px;
  padding-bottom: 10px;

  ul {
    display: flex;
    flex-wrap: wrap;

    span,
    a {
      font-size: 14px;
      color: $txt;
      display: inline-block;

      padding: 5px 0;
      line-height: 1;
      font-weight: 300;

      &:before {
        margin-right: 10px;
        color: $txt;
        content: "/";

      }
    }

    li:first-child {
      a:before {
        content: none;
      }
    }

    a {
      margin-right: 10px;


      svg {
        stroke: $txt;
        transition: all 0.3s ease-in;
      }


      &:focus,
      &:hover {
        outline: none;
        color: $main;

        svg {
          stroke: $main;

        }

        &:after {
          color: $txt;
        }

      }
    }
  }

  @media(min-width: $grid-breakpoints-md) {
    ul {

      a,
      span {
        font-size: 16px;
      }
    }
  }


}

@media(min-width: $grid-breakpoints-xl) {
  .page__header .breadcrumbs ul {

    a,
    span {
      font-size: 18px;
    }
  }
}

@keyframes fixedMenuDown {
  0% {
    top: -100px;
  }

  100% {
    top: 0;
  }
}

.menu-is-fixed {
  //padding-top: 150px;
}


.header {

  background: $main;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 90;
  width: 100%;
  color:#fff;

  &.fixed-header-scroll {
    position: fixed;
    top: 0;

    background: $main;
    -webkit-animation-name: fixedMenuDown;
    animation-name: fixedMenuDown;
    -webkit-animation-duration: .5s;
    animation-duration: .5s;
    left: 0;
    width: 100%;
    z-index: 100;
    box-shadow: 2px 2px 15px 0 rgb(0, 0, 0, 0.25);

    .header__top {
      display: none;
    }

    .header__logo img {
      filter: brightness(0) invert(1);
    }

    .header__wrapper {
      height: 60px;
      padding-top: 5px;
      padding-bottom: 5px;
    }

    .header__bottom:after {
      content: none;
    }

    .nav > ul > li > a {
      color: #fff;
    }

    .header__logo {
      bottom: 10px;

      img {
        width: 100px;
      }
    }


  }


}

.header__top {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size:12px;

}

.lang__list {
  display: flex;
  margin-right: 70px;

  a {
    display: block;
    padding: 0 10px;
    font-size: $font16;
    font-weight: 300;
    line-height: 1;
    color: #fff;

    &.active {
      font-weight: 700;
    }

    @media(hover: hover) {
      &:hover {
        color: #08ABED;
      }
    }
  }

  li:not(:last-child) {
    border-right: 1px solid #fff;
  }

}


.header__wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  padding-left: 220px;
  position: relative;
  height: 100px;
  padding-top: 10px;
  padding-bottom: 15px;

}


.header__logo {
  position: absolute;
  bottom: 0%;
  left: 20px;
  z-index: 10;
  transition: none;

  img {

    filter: brightness(0) invert(1);
  }
}

.headerForm {
  width: 48px;
  height: 48px;
  position: relative;
  margin-left: 40px;
  margin-right: auto;


  input {
    border: none;
    width: 100%;
    padding-right: 40px;
    font-size: 16px;
    padding-left: 20px;
    padding-top: 5px;
    padding-bottom: 5px;
    background: #fff;
    border-radius: 25px;
    font-weight: 600;
    min-height: 50px;
    border: 2px solid $line;

  }

  ::placeholder {
    color: $txt;
    font-weight: 600;
  }

  button {
    position: absolute;
    width: 48px;
    right: 0px;
    bottom: 0px;
    border: none;
    padding: 0;
    display: inline-block;
    background: transparent;
    height: 48px;
    background-image: url("../img/ico-search-main.svg");
    background-repeat: no-repeat;
    background-position: center;

  }
}


.header__accesibility {
  border: 1px solid $line;
  border-radius: 25px;
  padding: 0px 4px;
  display: flex;
  align-items: center;
  gap: 2px;
  margin-right: 30px;
  overflow: hidden;
  margin-left: auto;

  li {
    height: 48px;
  }

  a,
  button {
    display: inline-block;
    height: 100%;
    min-width: 48px;
    height: 48px;
    padding: 0;
    background-color: transparent;
  }
}


.link--add {
  background-image: url("../img/ico-add.svg");
  background-repeat: no-repeat;
  background-position: center;

}

.btn--contrast {
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../img/ico-contrast.svg");
  border: none;
  vertical-align: middle;

  span {
    min-width: 210px;
  }

}


.nav-toggle {
  width: 50px;
  padding: 0;
  padding-left: 50px;
  height: 50px;
  background: none;
  border: none;
  display: none;
  position: relative;
  overflow: hidden;
  color: #fff;
  font-size: $font20;
  left: -10px;
  line-height: 50px;

  strong {
    margin-left: -20px;
  }


  span {
    width: 26px;
    height: 2px;
    background: #fff;
    display: block;
    position: absolute;
    left: 12px;
    top: 15px;
    transition: all 0.3s ease-in-out;
    transform-origin: center;

    &:nth-child(2) {
      top: 25px;
    }

    &:nth-child(3) {
      top: 35px;
    }

  }

  &.open span {
    &:first-child {
      transform: rotate(45deg);
      top: 24px;
    }

    &:nth-child(2) {
      left: 50px;
      opacity: 0;
    }

    &:nth-child(3) {
      top: 24px;
      transform: rotate(-45deg);
    }
  }

}


.header__wrapper {
  //background: $main;
}

.header__bottom {
  display: flex;
  justify-content: space-between;
  position: relative;
  z-index: 10;
}

