@media(min-width: $grid-breakpoints-xl) {


  .nav > ul > li > a {
    color: #fff;
  }

  .menu__item--has-children {
    position: relative;

    ul {
      padding-top: 16px;
      position: absolute;
      display: block;
      left: -10px;
      height: 0;
      overflow: hidden;
      min-width: 250px;
      z-index: -130;
      top: 100%;
      transition: opacity 0.5s ease-in-out, left 0.3s ease-in-out;
      opacity: 0;


      li {
        background: $main2;

        border-top: none;

        &:first-child a {
          padding-top: 20px;
        }

        &:last-child a {
          padding-bottom: 20px;
        }
