          &:hover {
            background: $main2-hover
          }
        }

        &:focus-visible {
          outline-color: $main;
          outline-offset: -2px;
        }

      }

    }

    &.open ul {
      z-index: 30;
      height: initial;
      opacity: 1;
      left: 0;
    }
  }

  .menu__item--has-children:hover {
    ul {
      z-index: 30;
      height: initial;
      opacity: 1;
      left: 0;
    }
  }


