    column-gap: 30px;
    flex-wrap: wrap;
  }

  a {
    color: $main;
    position: relative;
    padding: 5px 0;
    font-size: $font18;
    font-weight: 700;
    display: block;

    &:before {
      content: "•  ";

    }

    &:after {
      content: "";
