.d-none {
    display: none !important;
}

.d-block {
    display: block !important;
}
.d-flex {
    display: flex !important;
}

.flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.overflow-hidden {
    overflow: hidden !important;
}
.relative {
    position: relative !important;
}
.container {
    padding-left: 20px;
    padding-right: 20px;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    max-width: $container-max-width-sm;

    @media (min-width: $grid-breakpoints-md) {
        max-width: $container-max-width-md;
    }
    @media (min-width: $grid-breakpoints-lg) {
        max-width: $container-max-width-lg;

        &.container--900 {
            max-width: 900px;
        }
    }
    @media (min-width: $grid-breakpoints-xl) {
        max-width: $container-max-width-xl;
    }
    @media (min-width: $grid-breakpoints-xxl) {
        max-width: $container-max-width-xxl;
    }
}


@media (min-width: $grid-breakpoints-lg) {
    .container--md {
        max-width: 800px;
    }
}

.container--500 {
    max-width: $container-max-width-sm;
}


.row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -15px;
    margin-right: -15px;
    width: calc(100% + 30px);
}

[class*="col-"] {
    padding-left: 15px;
    padding-right: 15px;
    width: 100%;
    margin-bottom: 30px;

    @media (max-width: $grid-breakpoints-sm) {
        padding-left: 8px;
        padding-right: 8px;
        margin-bottom: 16px;
    }
}


.row--16 {
    margin-left: -8px;
    margin-right: -8px;
    width: calc(100% + 16px);

    [class*="col-"] {
        padding-left: 8px;
        padding-right: 8px;
        width: 100%;
        margin-bottom: 16px;
    }
}

.row--60 {
    @media(min-width:$grid-breakpoints-xl) {
        margin-left: -30px;
        margin-right: -30px;
        width: calc(100% + 60px);

        [class*="col-"] {
            padding-left: 30px;
            padding-right: 30px;
            width: 100%;
            margin-bottom: 60px;
        }
    }
}

.w-100 {
    width: 100% !important;
    max-width: 100% !important;
    flex: 0 0 100% !important;
}

.col-6 {
    width: 50%;
    flex: 0 0 50%;
}

@media (min-width: $grid-breakpoints-sm) {

    .col-sm-4 {
        width: 33.33%;
        flex: 0 0 33.33%;
    }
    .col-sm-6 {
        width: 50%;
        flex: 0 0 50%;
    }
}


@media (min-width: $grid-breakpoints-md) {
    .col-md-3 {
        width: 35%;
        flex: 0 0 33.33%;
    }
    .col-md-4 {
        width: 33.33%;
        flex: 0 0 33.33%;
    }
    .col-md-50,
    .col-md-6 {
        width: 50%;
        flex: 0 0 50%;
    }
    .col-md-8 {
        width: 66.66%;
        flex: 0 0 66.66%;
    }
    .col-md-9 {
        width: 75%;
        flex: 0 0 75%;
    }

}


@media (min-width: $grid-breakpoints-lg) {
    .col-lg-2 {
        max-width: 16.66%;
        flex: 0 0 16.66%;
    }
    .col-lg-3 {
        width: 25%;
        flex: 0 0 25%;
    }
    .col-lg-4 {
        width: 33.33%;
        flex: 0 0 33.33%;
    }
    .col-lg-6 {
        max-width: 50%;
        flex: 0 0 50%;
    }
    .col-lg-8 {
        max-width: 66.6%;
        flex: 0 0 66.6%;
    }
}

@media (min-width: $grid-breakpoints-xl) {
    .col-xl-2 {
        max-width: 16.66%;
        flex: 0 0 16.66%;
    }

    .col-xl-3 {
        width: 25%;
        flex: 0 0 25%;
    }

    .col-xl-4 {
        width: 33.33%;
        flex: 0 0 33.33%;
    }

    .col-xl-8 {
        width: 66.66%;
        flex: 0 0 66.66%;
    }

    .col-xl-20 {
        width: 20%;
        flex: 0 0 20%;
    }
}



.section {

    padding-top: 80px;
    padding-bottom: 80px;

    // @media(max-width:$grid-breakpoints-md) {
