.tooltip {
  position: relative;
  cursor: pointer;
}
.tooltip span {
  opacity: 0;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%);
  background: #fff;
  z-index: -1000;
  box-shadow: 2px 2px 15px 0 rgba(0, 0, 0, 0.25);
  padding: 5px;
  font-size: 0.75rem;
  color: #000;
  width: 0;
  height: 0;
  overflow: hidden;
  text-align: center;
  line-height: 1.2;
}
.tooltip:hover span {
  opacity: 1;
  z-index: 1;
  height: auto;
  width: auto;
}

