}
.breadcrumbs ul span,
.breadcrumbs ul a {
  font-size: 14px;
  color: #000;
  display: inline-block;
  padding: 5px 0;
  line-height: 1;
  font-weight: 300;
}
.breadcrumbs ul span:before,
.breadcrumbs ul a:before {
  margin-right: 10px;
  color: #000;
  content: "/";
}
.breadcrumbs ul li:first-child a:before {
  content: none;
}
.breadcrumbs ul a {
  margin-right: 10px;
}
.breadcrumbs ul a svg {
  stroke: #000;
  transition: all 0.3s ease-in;
}
.breadcrumbs ul a:focus, .breadcrumbs ul a:hover {
  outline: none;
  color: #133353;
}
.breadcrumbs ul a:focus svg, .breadcrumbs ul a:hover svg {
  stroke: #133353;
}
.breadcrumbs ul a:focus:after, .breadcrumbs ul a:hover:after {
  color: #000;
}
