@keyframes fixedMenuDown {
  0% {
    top: -100px;
  }
  100% {
    top: 0;
  }
}
.header {
  background: #133353;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 90;
  width: 100%;
  color: #fff;
}
.header.fixed-header-scroll {
  position: fixed;
  top: 0;
  background: #133353;
  -webkit-animation-name: fixedMenuDown;
