@media (max-width: 992px) {
  #scrollDown {
    right: initial;
    left: 10px;
  }
}

.video__wrapper {
  height: 0;
  padding-top: 56%;
  position: relative;
  margin-top: -30px;
  margin-bottom: 100px;
}
.video__wrapper video {
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  z-index: 1;
  width: 100%;
}

.video__img {
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  z-index: 1;
  width: 100%;
  height: 100%;
}

.video__play {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 8;
  width: 60px;
  height: 60px;
  background-image: url("../img/play.svg");
  background-size: contain;
  background-position: center;
  background-color: transparent;
  border: none;
}

.video__title {
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translateX(-50%);
  color: #fff;
  text-transform: uppercase;
  font-size: 22px;
  text-align: center;
  z-index: 5;
  font-weight: 400;
  width: 100%;
}

@media (max-width: 768px) {
  .video__wrapper {
    margin-bottom: 50px;
  }

  .video__title {
    top: 70%;
    font-size: 16px;
  }
}